@import "_header";
@import "_footer";
html {
  width: 100%;
}

.wrapper {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  flex-shrink: 0;
}

.content {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
}

body {
  font-family: $font1;
  font-weight: 400;
  color: $black;
  &.fixed-body {
    overflow-y: hidden;
  }
}

.container {
  max-width: 1240px;
  margin: 0 auto;
  padding: 0 30px;
  @media screen and (max-width: 992px) {
    padding: 0 20px;
  }
}

@media screen and (max-width: 992px){
  .wrapper{
    padding-bottom: 80px;
  }
}
